:root {
  --white: #fff;
  --black: #000;
  --very-light-pink: #c7c7c7;
  --text-input-field: #f7f7f7;
  --dark: #232830;
  --hospital-green: #acd9b2;
  --sm: 14px;
  --md: 16px;
  --lg: 18px;
}

body {
  margin: 0;
  font-family: Quicksand, sans-serif;
}

.ProductItem {
  width: 140px;
}

.ProductItem img {
  width: 140px;
  height: 140px;
}

.product-info {
  justify-content: space-between;
  align-items: center;
  margin-top: 12px;
  display: flex;
}

.product-info figure {
  margin: 0;
}

.product-info figure img {
  width: 35px;
  height: 35px;
}

.product-info div p:nth-child(1) {
  font-weight: bold;
  font-size: var(--md);
  margin-top: 0;
  margin-bottom: 4px;
}

.product-info div p:nth-child(2) {
  font-size: var(--sm);
  color: var(--very-light-pink);
  margin-top: 0;
  margin-bottom: 0;
}

@media (min-width: 640px) {
  .cards-container {
    grid-template-columns: repeat(auto-fill, 140px);
  }

  .ProductItem {
    width: 240px;
  }

  .ProductItem img {
    width: 240px;
    height: 240px;
    object-fit: cover;
    border-radius: 20px;
  }
}

.ProductList {
  grid-template-columns: repeat(auto-fill, 140px);
  display: grid;
}

@media (min-width: 640px) {
  .ProductList {
    grid-template-columns: repeat(auto-fill, 240px);
    place-content: center;
    gap: 26px;
    margin: 3em 0 0;
  }
}

.Menu {
  width: 100px;
  height: auto;
  border: 1px solid var(--very-light-pink);
  background-color: var(--white);
  border-radius: 6px;
  padding: 20px 20px 0;
  position: absolute;
  top: 60px;
  right: 50px;
}

.Menu ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.Menu ul li {
  text-align: end;
}

.Menu ul li:nth-child(1), .Menu ul li:nth-child(2) {
  font-weight: bold;
}

.Menu ul li:last-child {
  border-top: 1px solid var(--very-light-pink);
  padding-top: 20px;
}

.Menu ul li:last-child a {
  color: var(--hospital-green);
  font-size: var(--sm);
}

.Menu ul li a {
  color: var(--back);
  margin-bottom: 20px;
  text-decoration: none;
  display: inline-block;
}

.OrderItem {
  grid-template-columns: auto 1fr auto auto;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
  display: grid;
}

.OrderItem figure {
  margin: 0;
}

.OrderItem figure img {
  width: 70px;
  height: 70px;
  object-fit: cover;
  border-radius: 20px;
}

.OrderItem p:nth-child(2) {
  color: var(--very-light-pink);
}

.OrderItem p:nth-child(3) {
  font-size: var(--md);
  font-weight: bold;
}

.OrderItem img {
  cursor: pointer;
}

.MyOrder {
  width: 100%;
  box-sizing: border-box;
  border: 1px solid var(--very-light-pink);
  background-color: #fff;
  border-radius: 6px;
  padding: 24px;
  animation: .26s ease-in-out swipeLeft;
  position: absolute;
  top: 60px;
  bottom: auto;
  right: 0;
}

.title-container {
  display: flex;
}

.title-container img {
  margin-right: 14px;
  transform: rotate(180deg);
}

.title {
  font-size: var(--lg);
  font-weight: bold;
}

.order {
  background-color: var(--text-input-field);
  border-radius: 8px;
  grid-template-columns: auto 1fr;
  align-items: center;
  gap: 16px;
  margin-bottom: 24px;
  padding: 0 24px;
  display: grid;
}

.order p:nth-child(1) {
  flex-direction: column;
  display: flex;
}

.order p span:nth-child(1) {
  font-size: var(--md);
  font-weight: bold;
}

.order p:nth-child(2) {
  text-align: end;
  font-weight: bold;
}

.primary-button {
  background-color: var(--hospital-green);
  color: var(--white);
  width: 100%;
  cursor: pointer;
  font-size: var(--md);
  height: 50px;
  border: none;
  border-radius: 8px;
  font-weight: bold;
}

@keyframes swipeLeft {
  0% {
    width: 0;
  }

  100% {
    width: 320px;
  }
}

@media (min-width: 640px) {
  .MyOrder {
    width: 360px;
  }
}

nav {
